*,*:after,*:before{
    box-sizing: border-box;
}

body{
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 100vh;
    background: #e5eff8;
}

.setting{
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: relative;
    box-shadow: 0.2rem 0.2rem 0.5rem #477a8550;
    padding:0.5rem;
}

input[type="checkbox"]{
    visibility: hidden;
    position:absolute;
    left: 100rem;
}

label{
    display: inline-block;
    width: 4rem;
    height: 1.5rem;
    background-color: #e05045;
    border-radius: 1rem;
    position: relative;
    cursor: pointer;
    transition: 0.5s;
    box-shadow: 0 0 0.5rem #477a8550;
}

label::after {
    content: "";
    width: 1rem;
    height: 1rem;
    background-color: #e8f5f7;
    position: absolute;
    border-radius: 0.5rem;
    top: 0.25rem;
    left: 0;
    transition: 0.5s;
  }

  input[type="checkbox"]:checked + label:after {
    left:4rem;
    transform: translateX(-100%);
  }
  
  input:checked + label {
    background-color: #477a85;
  }

  .btn{
    display: inline-block;
    width:2rem;
    height:2rem;
    border: none;
    color:white;
    border-radius:1rem;
    background-color: #477a85;
    &:hover{
        background-color: #243d42;
    }
}

.circle{
    padding:0;
    margin-bottom:1rem;
    position: relative;
    box-shadow: 0.2rem 0.2rem 0.5rem gray;
    svg{
        width: 200px;
        height: 200px;    
        transform: rotate(-90deg);
        overflow: initial;
        margin: 0;
    } 

    .svg{
        stroke-width:10px;
        fill:none;
        stroke-dashoffset:440;
        stroke-dasharray:440;
// back here    

}
}

.svg__inner{
    stroke: #5247eb; 
    stroke-dashoffset:0;
    
   }

.svg__outer{
    stroke: #ddd7d7;
    position: relative;
    stroke-dashoffset:0;
    z-index: 1;
    //stroke-dashoffset:calc(440 - ( 440 * 120 )/120); 
}

.timmer{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 2rem;
    font-weight: bold;
    color: #2849da;
}

